import Card from "../components/UI/Card";
import CardContent from "../components/UI/CardsContent";
import Title from "../components/UI/Title";
import Layout from "../components/common/Layout";

function Home() {
  return (
    <Layout>
      <Title
        title="Hi. I'm Ismael."
        subTitle="A software developer."
        detail="I'm passionate about developing software solutions that are engaging,
          accessible, and user-centric."
      />
      <CardContent>
        <Card title="Demo" subTitle="Subtitle" col={5} color="bg-teal-300" />
        <Card title="Demo" subTitle="Subtitle" col={7} color="bg-orange-200" />
      </CardContent>
    </Layout>
  );
}

export default Home;
